/*
 * Component: scroll
 * ------------------
 */
//Main scroll

/*滚动条 start*/  
::-webkit-scrollbar {  
  width: 6px;  
  height: 4px;  
  background-color: #F5F5F5;  
}  
/*定义滚动条轨道 内阴影+圆角*/  
::-webkit-scrollbar-track {  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);  
  background: #fff ;  
}  
/*定义滑块 内阴影+圆角*/  
::-webkit-scrollbar-thumb {  
  border-radius: 2px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color:rgba(73, 142, 210, .4);  
}  
::-webkit-scrollbar-thumb:hover {  
  border-radius: 2px;  
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);  
  background-color:rgba(73, 142, 210, .7);  
} 
